.contador {
    min-width: 2px;
    height: 70px;
    font-size: 50px;
    display: grid;
    align-items: center;
    justify-content: center;
    margin-bottom: 15px ;
    border-radius: 45px;
    display: flex;
    flex-direction: center; /* Aliear buttons vertical */
    align-items: center;    /* Centrar buttons horizontal */
    justify-content: center; /* Centrar buttons vertical */
    height: 10vh; /* Hace que el contenedor esté a toda altura */
}